import React from 'react';
import PropTypes from 'prop-types';
import PortfolioBody from 'components/atoms/PortfolioBody';
import Modal from 'components/atoms/Modal';
import 'styles/portfolioBody.scss';

const PortfolioModal = props => {
    const {open, data, width, height, onClickCloseModal, className} = props;

    return (
        <React.Fragment>
            <Modal
                open={open}
                onClickCloseModal={onClickCloseModal}
                body={<PortfolioBody data={data} />}
                width={width}
                height={height}
                className={className}
            />
        </React.Fragment>
    );
};

PortfolioModal.propTypes = {
    onClickCloseModal: PropTypes.func,
    className: PropTypes.string,
    open: PropTypes.bool,
    width: PropTypes.string,
    height: PropTypes.string,
    data: PropTypes.shape({
        mainTitle: PropTypes.string,
        subTitle: PropTypes.string,
        task: PropTypes.string,
        period: PropTypes.string,
        front: PropTypes.string,
        back: PropTypes.string,
        com: PropTypes.string,
    }),
};

PortfolioModal.defaultProps = {
    className: '',
    width: '100%',
    height: '100%',
    open: false,
    onClickCloseModal: () => {},
    data: {},
};

export default PortfolioModal;
