import React from "react";
import classnames from "classnames";
import PropTypes from "prop-types";
import "styles/modal.scss";

const Modal = props => {
    const { open, body, width, height, onClickCloseModal, className } = props;

    // 모달 - 창 닫기
    const onClose = e => {
        onClickCloseModal(e);
    };

    return (
        <React.Fragment>
            <div
                className={classnames(`custom-modal-root ${className}`, {
                    open
                })}
            >
                <div className="custom-modal-background-root" onClick={e => { onClose(e) }} />
                <div className="custom-modal-frame"
                    style={{
                        width,
                        height
                    }}
                >
                    <div className="custom-modal-header">
                        <ul className="circle">
                            <li className="red" onClick={e => { onClose(e) }}>&nbsp;</li>
                            <li className="yellow">&nbsp;</li>
                            <li className="green">&nbsp;</li>
                        </ul>
                    </div>
                    <div className="custom-modal-contents">
                        {/* <FontAwesomeIcon
                        icon={faWindowClose}
                        size="1x"
                        className="closeIcon"
                        onClick={e => { onClose(e) }}
                    /> */}
                        {body}
                    </div>
                </div>
            </div>
        </React.Fragment >
    );
};

Modal.propTypes = {
    onClickCloseModal: PropTypes.func,
    body: PropTypes.oneOfType([
        PropTypes.element,
    ]),
    className: PropTypes.string,
    open: PropTypes.bool,
    width: PropTypes.string,
    height: PropTypes.string,
};

Modal.defaultProps = {
    body: <div />,
    className: "",
    width: "100%",
    height: "100%",
    open: false,
    onClickCloseModal: () => { }
};

export default Modal;