import React from "react";
import PropTypes from "prop-types";
import classnames from 'classnames';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFolder, faFolderOpen } from '@fortawesome/free-solid-svg-icons';
import _ from 'lodash';
import "styles/project.scss";

const Project = props => {
    const { k, company, portfolio, itemRef, onClickCompany, onClickProject } = props;

    return (
        <React.Fragment>
            <div className="contents-container">
                <h2>PROJECTS</h2>
                <div className="controls-portfolio">
                    <ul>
                        {company && company.map((row, index) => (
                            <li key={`portfolid-company-${index}`}
                                data-key={row}
                                className={classnames({
                                    on: row === k
                                })}
                                onClick={e => onClickCompany(e, row)}
                            >
                                <a href="#!" onClick={e => e.preventDefault()}>{row}</a>
                            </li>
                        ))}
                    </ul>
                </div>
                <div className="list-portfolio" data-k={k}>
                    {portfolio && portfolio.map((row, index) => (
                        <div key={row + index}
                            ref={itemRef.current[index]}
                            className={classnames("item", {
                                visible: row.visible
                            })}
                            data-id={row.id}
                            data-key={row.key}
                            data-title={row.title}
                            style={row.style}
                        >
                            <div className="window">
                                <div className="window-header">
                                    <ul className="circle">
                                        <li className="red">&nbsp;</li>
                                        <li className="yellow">&nbsp;</li>
                                        <li className="green">&nbsp;</li>
                                    </ul>
                                </div>
                                <div className="window-body" onClick={e => row.visible && onClickProject(row)}>
                                    <p className="image">
                                        <FontAwesomeIcon icon={faFolder} size="3x" color="#64c6f8" className="img-close" />
                                        <FontAwesomeIcon icon={faFolderOpen} size="3x" color="#64c6f8" className="img-open" />
                                    </p>
                                    <p className="text">
                                        <span>{_.padStart(_.parseInt(index) + 1, 2, "0")}.</span> {row.title}
                                    </p>
                                </div>
                            </div>

                        </div>
                    ))}
                </div>
            </div>
        </React.Fragment >
    );
};

Project.propTypes = {
    k: PropTypes.string,
    itemRef: PropTypes.oneOfType([
        PropTypes.func, // for legacy refs
        PropTypes.shape(React.MutableRefObjec)
    ]),
    company: PropTypes.oneOfType([
        PropTypes.object,
        PropTypes.array,
    ]),
    portfolio: PropTypes.arrayOf(PropTypes.object),
    onClickCompany: PropTypes.func,
    onClickProject: PropTypes.func,
};

Project.defaultProps = {
    k: "all",
    itemRef: () => { },
    company: [],
    portfolio: [],
    onClickCompany: () => { },
    onClickProject: () => { },

};

export default Project;