import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import Main from 'Main';

const AppRouter = () => (
    <React.Fragment>
        <BrowserRouter>
            <Switch>
                <Route exact path="/" component={Main} />
            </Switch>
        </BrowserRouter>
    </React.Fragment>
);

export default AppRouter;
