import React from 'react';
import PropTypes from 'prop-types';

const PortfolioBody = props => {
    const {data} = props;

    return (
        <React.Fragment>
            <div className="detail-container">
                <div className="main-title">{data.mainTitle}</div>
                <div className="sub-title">{data.subTitle}</div>
                <ul>
                    {data.task && (
                        <li className="task">
                            <div className="key">주요업무</div>
                            <div className="value">{data.task}</div>
                        </li>
                    )}
                    {data.period && (
                        <li className="period">
                            <div className="key">프로젝트 기간</div>
                            <div className="value">{data.period}</div>
                        </li>
                    )}
                    {data.front && (
                        <li className="technology">
                            <div className="key">프론트엔드</div>
                            <div className="value">{data.front}</div>
                        </li>
                    )}
                    {data.back && (
                        <li className="technology">
                            <div className="key">백엔드</div>
                            <div className="value">{data.back}</div>
                        </li>
                    )}
                    {data.com && (
                        <li className="technology">
                            <div className="key">공통</div>
                            <div className="value">{data.com}</div>
                        </li>
                    )}
                </ul>
            </div>
            ;
        </React.Fragment>
    );
};

PortfolioBody.propTypes = {
    data: PropTypes.shape({
        mainTitle: PropTypes.string,
        subTitle: PropTypes.string,
        task: PropTypes.string,
        period: PropTypes.string,
        front: PropTypes.string,
        back: PropTypes.string,
        com: PropTypes.string,
    }),
};

PortfolioBody.defaultProps = {
    data: {},
};

export default PortfolioBody;
