import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import 'styles/history.scss';

const History = props => {
    const {data, originalHistoryLength, viewIndex, onClickMore} = props;

    return (
        <React.Fragment>
            <div className="history-container">
                <h2>HISTORY</h2>
                <ul className="timeLine">
                    {data &&
                        data.map((row, index) => (
                            <li key={`time-line-key-${index}`} className="item">
                                <div className="panel">
                                    <dl>
                                        <dt>{row.date}</dt>
                                        <dd className="title">{row.title}</dd>
                                        <dd className="desc">
                                            {row.desc.map((d, i) => (
                                                <div key={`desc-${i}`}>
                                                    {d ? `-${d}` : ''}
                                                </div>
                                            ))}
                                        </dd>
                                    </dl>
                                </div>
                            </li>
                        ))}
                </ul>
                <div className="btn-area">
                    <a
                        href="#!"
                        onClick={onClickMore}
                        className={classnames('btn-more-history', {
                            view: originalHistoryLength >= viewIndex,
                        })}
                    >
                        더보기
                    </a>
                </div>
            </div>
        </React.Fragment>
    );
};

History.propTypes = {
    data: PropTypes.arrayOf(PropTypes.object),
    onClickMore: PropTypes.func,
    originalHistoryLength: PropTypes.number,
    viewIndex: PropTypes.number,
};

History.defaultProps = {
    data: [],
    onClickMore: () => {},
    originalHistoryLength: 0,
    viewIndex: 0,
};

export default History;
