import React, {
    useState,
    useMemo,
    useEffect,
    useLayoutEffect,
    useRef,
} from 'react';
import classnames from 'classnames';
import _ from 'lodash';
import oriItem, { oriItemDetail } from 'data/project';
import history from 'data/history';
import Loading from 'Loading';
import PortfolioModal from 'components/organism/PortfolioModal';
import IntroItem from 'components/molecule/IntroItem';
import History from 'components/molecule/History';
import Project from 'components/molecule/Project';
import Contact from 'components/molecule/Contact';
import PulseCircle from 'components/atoms/PulseCircle';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowCircleUp } from '@fortawesome/free-solid-svg-icons';
// import Night from "components/atoms/Night";

const Main = props => {
    const company = useMemo(
        () => [
            'all',
            'reverselab',
            'koreacenter',
            'hakyungsoft',
            'kocore',
            // 'etc',
        ],
        []
    );
    const [key, setKey] = useState('all');
    const [portfolio, setPortfolio] = useState(oriItem);
    const [open, setOpen] = useState(false);
    const [modalData, setModalData] = useState({});
    const [viewIndex, setViewIndex] = useState(2);
    const [historyData, setHistoryData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [navOption, setNavOption] = useState({ fixed: false });
    const itemRef = useRef([]);
    const containerRef = useRef(null);
    itemRef.current = oriItem.map(
        (_v, i) => itemRef.current[i] ?? React.createRef()
    );

    // PROJECTS를 필터링후, 위치이동값 계산하여 상태값을 갱신해주는 함수
    const setFilterItem = k => {
        const itemPos = JSON.parse(window.sessionStorage.getItem('itemPos'));
        const newItem = [];
        const arrO = [];
        const arrX = [];
        Array.from(itemRef.current).forEach((v, i) => {
            const item = v.current;
            const same = item.dataset.key === k || k === 'all' || false;
            const oriX = item.offsetLeft;
            const oriY = item.offsetTop;

            let p = 0;
            if (same) {
                p = arrO.length;
            } else {
                p = itemPos.length - arrX.length - 1;
            }

            const itemX = itemPos[p]?.x;
            const itemY = itemPos[p]?.y;
            let x = 0;
            if (oriX !== itemX) {
                if (oriX > itemX) {
                    x = -1 * (oriX - itemX);
                } else {
                    x = itemX - oriX;
                }
            }
            let y = 0;
            if (oriY !== itemY) {
                if (oriY > itemY) {
                    y = -1 * (oriY - itemY);
                } else {
                    y = itemY - oriY;
                }
            }

            const info = {
                el: item,
                oriX,
                oriY,
                visible: same,
                id: item.dataset.id,
                key: item.dataset.key,
                title: item.dataset.title,
                style: {
                    transform: `translate3d(${x}px,${y}px,0)`,
                },
            };

            if (same) {
                arrO.push(info);
            } else {
                arrX.push(info);
            }

            newItem.push(info);
        });
        setPortfolio(k === 'all' ? oriItem : newItem);
    };

    // offset계산 함수
    const calcOffset = () => {
        const tmp = [];
        document
            .querySelectorAll('.list-portfolio div.item')
            .forEach((v, i) => {
                tmp[i] = { x: v.offsetLeft, y: v.offsetTop };
            });
        window.sessionStorage.setItem('itemPos', JSON.stringify(tmp));
    };

    useEffect(() => {
        setHistoryData(
            history && history.filter((_d, index) => index <= viewIndex)
        );
        // eslint-disable-next-line
    }, []);

    // 창크기 상태값 변동시 실행
    useLayoutEffect(() => {
        const updateSize = async () => {
            await calcOffset();
            await setFilterItem(key);
        };
        updateSize();

        window.addEventListener('resize', updateSize);
        return () => {
            window.removeEventListener('resize', updateSize);
        };

        // eslint-disable-next-line
    }, [key]);

    // 더보기 시도시, 실행
    useEffect(() => {
        const loadingHandle = async () => {
            await setLoading(true);
            await setHistoryData(
                history && history.filter((_d, index) => index <= viewIndex)
            );
            await setTimeout(() => {
                setLoading(false);
            }, 500);
        };

        if (viewIndex > 2) {
            loadingHandle();
        }
    }, [viewIndex]);

    // PROJECTS에서 필터할 아이템 클릭시 실행되는 함수
    const onClickFilterItem = (e, k) => {
        if (k !== key) {
            setKey(k);
        }
    };

    // 모달 클릭 이벤트
    const onClickProject = row => {
        setModalData(oriItemDetail.filter(o => o.id === Number(row.id))[0]);
        setOpen(true);
    };

    // scrolll 이벤트
    useEffect(() => {
        // 컨테이너박스내에서 스크롤시 실행
        const onScrollContainer = e => {
            const { top } = containerRef.current.getBoundingClientRect();
            const fixed = top < -100 && true;
            setNavOption(s => ({ ...s, ...{ fixed } }));
        };

        (() => {
            window.addEventListener('scroll', onScrollContainer);
        })();

        return () => {
            window.removeEventListener('scroll', onScrollContainer);
        };
    }, []);

    return (
        <React.Fragment>
            <div className="container" ref={containerRef}>
                <PortfolioModal
                    className=""
                    open={open}
                    data={modalData}
                    width="50%"
                    height="50%"
                    onClickCloseModal={e => setOpen(false)}
                />
                {/* <Night /> */}
                <header
                    className={classnames('top-nav', {
                        fixed: navOption.fixed === true,
                    })}
                >
                    <div className="nav__container">
                        <nav>
                            <ul>
                                <li>
                                    <a
                                        href="#!"
                                        onClick={e => {
                                            e.preventDefault();
                                            document
                                                .querySelector('#skills')
                                                .scrollIntoView({
                                                    behavior: 'smooth',
                                                });
                                        }}
                                    >
                                        SKILLS
                                    </a>
                                </li>
                                <li>
                                    <a
                                        href="#!"
                                        onClick={e => {
                                            e.preventDefault();
                                            document
                                                .querySelector('#history')
                                                .scrollIntoView({
                                                    behavior: 'smooth',
                                                });
                                        }}
                                    >
                                        HISTORY
                                    </a>
                                </li>
                                <li>
                                    <a
                                        href="#!"
                                        onClick={e => {
                                            e.preventDefault();
                                            document
                                                .querySelector('#projects')
                                                .scrollIntoView({
                                                    behavior: 'smooth',
                                                });
                                        }}
                                    >
                                        PROJECTS
                                    </a>
                                </li>
                                <li>
                                    <a
                                        href="#!"
                                        onClick={e => {
                                            e.preventDefault();
                                            document
                                                .querySelector('#contact')
                                                .scrollIntoView({
                                                    behavior: 'smooth',
                                                });
                                        }}
                                    >
                                        CONTACT
                                    </a>
                                </li>
                            </ul>
                        </nav>
                    </div>
                </header>
                <div className="top">
                    <div
                        className={classnames('top__container', {
                            backgroundColorChange: navOption.fixed === true,
                        })}
                    >
                        <div className="main">
                            <div className="title">
                                <h3>
                                    WELCOME TO <span>YJ PORTFOLIO</span>
                                </h3>
                                <br />
                                <PulseCircle />
                                <span className="sub-title">
                                    새로운 도약을 꿈꾸는 프론트엔드
                                    개발자입니다.
                                </span>
                                <div className="btn-area">
                                    <a
                                        href="#!"
                                        onClick={e => {
                                            e.preventDefault();
                                            document
                                                .querySelector('#skills')
                                                .scrollIntoView({
                                                    behavior: 'smooth',
                                                });
                                        }}
                                        className="btn-more"
                                    >
                                        더알아보기
                                    </a>
                                </div>
                                <div className="btn-arrow-area">
                                    <a
                                        href="#!"
                                        onClick={e => {
                                            e.preventDefault();
                                            document
                                                .querySelector('#skills')
                                                .scrollIntoView({
                                                    behavior: 'smooth',
                                                });
                                        }}
                                        className="btn-arrow"
                                    >
                                        &nbsp;
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="skills" className="intro">
                    <IntroItem />
                </div>
                <section id="history" className="history">
                    <History
                        data={historyData}
                        originalHistoryLength={history.length}
                        viewIndex={viewIndex}
                        onClickMore={async e => {
                            e.preventDefault();
                            await setViewIndex(prev => prev * 2);
                            await setFilterItem(key);
                        }}
                    />
                </section>
                <section id="projects" className="contents">
                    <Project
                        k={key}
                        itemRef={itemRef}
                        company={company}
                        portfolio={portfolio}
                        onClickCompany={onClickFilterItem}
                        onClickProject={onClickProject}
                    />
                </section>
                <section id="contact" className="tail">
                    <Contact />
                </section>
                <footer>
                    <div className="copyright">
                        © 2021 All Rights Reserved. - Created by YunJeong
                    </div>
                </footer>
                <a
                    id="btn__top"
                    className={classnames('top-nav', {
                        view: navOption.fixed === true,
                    })}
                    href="#!"
                    onClick={e => {
                        e.preventDefault();
                        document.querySelector('.container').scrollIntoView({
                            behavior: 'smooth',
                        });
                    }}
                >
                    <span>
                        <FontAwesomeIcon icon={faArrowCircleUp} color="#df405a" size="3x" />
                    </span>
                </a>
                {loading && <Loading />}
            </div>
        </React.Fragment>
    );
};

export default Main;
